
#v-header {
  background: url('./vheader.jpg') no-repeat top center fixed;
  background-size: cover;
  width: 100%;
  height: 100vh;
}
#v-header h1, h2, p {
  margin: 0;
  padding: 0;
  font-family: 'Bubbler One', sans-serif;
}
#v-header h2, p {
  font-family: 'Raleway', sans-serif;
  font-size: 26px;
}



